  <div id="app">
    <el-container>
      <el-header>OKX Connect</el-header>
      <el-main>

        <!-- 连接钱包 -->
        <el-card class="wallet-actions">
          <el-row :gutter="12" class="button-row">
            <el-col :span="24">
              <el-button
                type="primary"
                @click="connectBtnClicked"
                icon="el-icon-connection"
                :disabled="walletAddress.length != 0"
                >{{ connectStatusStr }}</el-button
              >
            </el-col>
          </el-row>

          <el-row :gutter="12" class="button-row">
            <el-col :span="24">
              <el-button
                type="danger"
                @click="disconnectAction"
                icon="el-icon-switch-button"
                :disabled="walletAddress.length == 0"
                >{{ disconnectStatusStr }}</el-button
              >
            </el-col>
          </el-row>
        </el-card>

      </el-main>
    </el-container>
  </div>
</template>

