<script>

import {OKXUniversalConnectUI} from "@okxconnect/ui";
import VConsole from 'vconsole';

new VConsole();

// import QrcodeVue from 'qrcode.vue'
export default {
  name: "App",

  data: () => {
    return {
      activeTab: "eip155:1",
      testTopic: "",
      testPublicKey: "",
      testProofPayload: "",
      tonProofValue:"",
      pendingDeepLink: "-",
      logList: [],
      connectReady: false,
      connectStatusStr: "Connect",
      disconnectStatusStr: "Disconnect",
      localPubKey: "",
      localSecKey: "",
      testTransaction: "",
      selectedChain: "eip155:1",
      testTransactionStr: "Send",
      walletAddress: "",
      testboc: "",
      ProofPayloadVerify: "",
      showModal: false,
      showVerifyRow: false,
      currentStatus: "",
    };
  },
  computed: {
    getWalletAddress: function () {
      return this.walletAddress;
    },
  },
  methods: {
    isChecked(chainId) {
      return this.selectedChains.has(chainId);
    },

    initChains(newChains) {
      if (newChains.length > 0) {
        const updatedChains = Object.assign({}, this.availableChains);
        for (const chainId of newChains) {
          if (updatedChains[chainId]){
            updatedChains[chainId].checked = !updatedChains[chainId].checked;
          }
        }
        this.availableChains = updatedChains;
      }

      this.areChainsInit = true;
    },

    async clickActiveTab(tab) {
      const tabName = tab.props ? tab.props.name : tab.name;
      this.selectedChain = tabName;
      console.log(`已切换到链 :`, tabName);

      if (!window.provider) {
        console.error("钱包 未初始化完成");
      }
      window.provider.setDefaultChain(this.selectedChain);
    },

    async connectBtnClicked() {
      if (window.provider) {
        this.testTonSdk();
      } else {
        console.log(`connectBtnClicked provider not finish init`);
      }
    },

    async disconnectAction() {
      try {
        console.log("get disconnect wallet btn clicked");
        var provider = window.provider;
        await provider.disconnect();
        this.walletAddress = "disconnected";
      } catch (error) {
        console.log("Disconnect  error ==>> ", error.message);
        this.currentStatus = error.message;
      }
    },

    async testTonSdk() {
      let nameSpacesChains = ['eip155:1'];

      console.log(`namespaceChains  ===> ${JSON.stringify(nameSpacesChains)}`);
      var connectData = {
        namespaces: {
          eip155: {
            chains: nameSpacesChains.filter((item) =>
              item.startsWith("eip155")
            ),
            defaultChain: nameSpacesChains.filter((item) =>
              item.startsWith("eip155")
            )[0],
          }
        }
      };

      await window.provider.openModal(connectData)
        .then(() => {
          // 2. set defaul chain
          this.walletAddress = "connected";
          if (this.isPC()) {
            this.showModal = false;
          }
        })
        .catch((error) => {
          console.error("发生了错误：", error);
        });
    },

    closeModal() {
      this.showModal = false;
    },

    isPC() {
      const userAgentInfo = navigator.userAgent;
      console.log("current device ua is ===>", userAgentInfo);

      const Agents = [
        "Android",
        "iPhone",
        "SymbianOS",
        "Windows Phone",
        "iPad",
        "iPod",
      ];
      let flag = true;
      for (let v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
          flag = false;
          break;
        }
      }
      return flag;
    },
    isSafariOrChrome() {
      const userAgentInfo = navigator.userAgent;
      const Agents = ["Safari", "Chrome"];
      let flag = false;
      for (let v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
          flag = true;
          break;
        }
      }
      return flag;
    },
  },
  mounted() {
    var scriptTag = document.createElement("script");
    scriptTag.src = "https://telegram.org/js/telegram-web-app.js";
    scriptTag.id = "tgsdk";
    document.getElementsByTagName('head')[0].appendChild(scriptTag);

    this.connectReady = true;
    document.title = "OKX Connect";
    
    OKXUniversalConnectUI.init({
      dappMetaData: {
        icon: "https://static.okx.com/cdn/assets/imgs/247/58E63FEA47A2B7D7.png",
        name: "OKX Connect UI Test",
      },
      actionsConfiguration: {
        returnStrategy: "back",
        modals: "all",
      },
      language: "en_US"
    })
    .then((provider) => {
      window.provider = provider;
      console.log("init finished:", provider);
      console.log("init finished session:", JSON.stringify(provider.session));

      provider.on("display_uri", (uri) => {
        if (this.isPC()) {
          if (uri && !this.withUI) {
            this.qrValue = uri;
            this.showModal = true;
          }
        }
        console.log("通知>>>display_uri  ==> ", uri);
      });

      provider.on("connect", (session) => {
        console.log("通知>>>get connect session", session);
      });
      provider.on("reconnect", (session) => {
        console.log("通知>>>get reconnect session", session);
        this.walletAddress = "connected";
      });
      provider.on("disconnect", ({ topic }) => {
        console.log(`通知>>>get disconnect  ${topic}`);
        this.walletAddress = "disconnecteddddd";
      });
      provider.on("session_delete", ({ topic }) => {
        console.log(`通知>>>get session_delete  ${topic}`);
      });
      provider.on("session_update", (session) => {
        console.log(`通知>>>get session_update `, JSON.stringify(session));
      });
    });
  },
};
</script>
